@import "../../assets/styles/setup/setup";

.user__tab {
  padding: 10px 12px;
  transition: 0.4s;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  svg {
    color: white !important;
  }
  &:hover {
    background-color: rgba(blue, 0.4);
  }
  &.active {
    background-color: rgba(blue, 0.4);
    svg {
      fill: white !important;
    }
  }
}

.user__tab__container {
  background-color: rgba($black, 0.4);
  height: max-content;
  margin-top: 20px;
}
